<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin} from '@vuemap/vue-amap-util';

export default defineComponent({
  name: 'ElAmapControlHawkEye',
  mixins: [registerMixin],
  props: {
    autoMove: {
      type: Boolean,
      default: true
    }, // 
    showRectangle: {
      type: Boolean,
      default: true
    }, // 
    showButton: {
      type: Boolean,
      default: true
    }, // 
    isOpen: {
      type: Boolean,
      default: true
    }, // 
    mapStyle: {
      type: String
    }, // 'amap://styles/dark'
    layers: {
      type: Array
    }, // 
    width: {
      type: String
    }, // CSS'200px'
    height: {
      type: String
    }, // CSS'200px'
    offset: {
      type: Array
    }, //  [2,2]
    borderStyle: {
      type: String
    }, // CSS"double solid solid double"
    borderColor: {
      type: String
    }, // CSS'silver'
    borderRadius: {
      type: String
    }, // CSS'5px'
    borderWidth: {
      type: String
    }, // CSS'2px'
    buttonSize: {
      type: String
    } // CSS'12px'
  },
  data() {
    return {
    };
  },
  methods: {
    __initComponent(options) {
      return new Promise<void>((resolve) => {
        this.$parentComponent.plugin(['AMap.HawkEye'], () => {
          this.$amapComponent = new AMap.HawkEye(options);
          this.$parentComponent.addControl(this.$amapComponent);
          resolve();
        });
      });
    },
    destroyComponent() {
      if (this.$amapComponent && this.$parentComponent) {
        if(!this.parentInstance.isDestroy){
          this.$parentComponent.removeControl(this.$amapComponent);
        }
        this.$amapComponent = null;
        this.$parentComponent = null;
      }
    },
    __isOpen(flag){
      flag === false ? this.$amapComponent.close() : this.$amapComponent.open();
    }
  },
  render(){
    return null;
  }
});
</script>
