<template>
  <div style="display: none;">
    <div ref="info">
      <slot />
    </div>
  </div>
</template>
<script lang="ts">
import {defineComponent} from 'vue';
import {registerMixin} from '@vuemap/vue-amap-util';

export default defineComponent({
  name: 'ElAmapInfoWindow',
  mixins: [registerMixin],
  props: {
    isCustom: {
      type: Boolean,
      default: false
    }, // truecontentfalsecontent
    autoMove: {
      type: Boolean,
      default: true
    }, // 
    avoid: {
      type: Array
    }, // autoMove  true  [20, 20, 20, 20]
    closeWhenClickMap: {
      type: Boolean,
      default: false
    }, //  [0,0] Markerpositionmarkeranchoranchorpositionmarkerpositionmarker
    content: {
      type: [String, Object]
    }, // HTMLHTMLElement
    size: {
      type: Array
    }, // isCustomtrue
    anchor: {
      type: String
    }, // 'bottom-center''top-left'|'top-center'|'top-right'|'middle-left'|'center'|'middle-right'|'bottom-left'|'bottom-center'|'bottom-right'
    offset: {
      type: Array
    }, // : [0, 0]
    position: {
      type: Array
    } // 
  },
  emits: ['update:visible'],
  data() {
    return {
      converters: {
      }
    };
  },
  unmounted() {
    if(this.$amapComponent){
      this.$amapComponent.close();
    }
  },
  updated() {
    if(!this.content && this.$amapComponent){
      this.$amapComponent.setContent(this.$refs.info);
    }
  },
  methods: {
    __initComponent(options) {
      if (!options.content) {
        options.content = this.getSlotContent();
      }
      this.$amapComponent = new AMap.InfoWindow(options);
      this.$amapComponent.on('close', () => {
        this.$emit('update:visible', false);
      });
      if (this.visible !== false) {
        this.$amapComponent.open(this.$parentComponent, this.position);
      }
      if(!this.content && this.$amapComponent){
        // 
        const config = { attributes: true, childList: true, subtree: true };
        // 
        const observer = new MutationObserver(() => {
          this.$amapComponent.setContent(this.getSlotContent());
        });
        observer.observe(this.$refs.info as Node, config);
        this.observer = observer;
      }
    },
    getSlotContent(){
      return (this.$refs.info as HTMLDivElement);
    },
    destroyComponent() {
      if(this.observer){
        this.observer.disconnect();
        this.observer = null;
      }
      if(this.$amapComponent){
        if(this.$amapComponent.getIsOpen()){
          this.$amapComponent.close();
        }
        this.$amapComponent = null;
        this.$parentComponent = null;
      }
    },
    __position(position){
      if (this.visible) {
        this.$amapComponent.open(this.$parentComponent, position);
      } else {
        this.$amapComponent.setPosition(position);
      }
    },
    __visible(flag) {
      // fixed Amap info-window reopen
      const position = this.$amapComponent.getPosition();
      if (position) {
        flag === false ? this.$amapComponent.close() : this.$amapComponent.open(this.$parentComponent, [position.lng, position.lat]);
      }
    },
  },
});
</script>
