<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin} from '@vuemap/vue-amap-util';

export default defineComponent({
  name: 'ElAmapLayerDistrict',
  mixins: [registerMixin],
  props: {
    type: {
      type: String,
      default: 'Country',
      validator(value : string) {
        // 
        return ['World', 'Country', 'Province'].indexOf(value) !== -1;
      }
    },
    adcode: {
      type: String
    }, //  adcodehttps://a.amap.com/lbs/static/file/AMap_adcode_citycode.xlsx.zip
    SOC: {
      type: String
    }, // , https://a.amap.com/jsapi_demos/static/demo-center/js/soc-list.json
    depth: {
      type: Number
    }, // depth0depth1 depth2
    zooms: {
      type: Array
    }, //  [2-30]
    opacity: {
      type: Number
    }, //  1
    styles: {
      type: Object
    }//  styles* function
  },
  data() {
    return {
    };
  },
  methods: {
    __initComponent(options) {
      if ((this as any).type === 'World') {
        this.$amapComponent = new AMap.DistrictLayer.World(options);
      } else if ((this as any).type === 'Country') {
        this.$amapComponent = new AMap.DistrictLayer.Country(options);
      } else if ((this as any).type === 'Province') {
        this.$amapComponent = new AMap.DistrictLayer.Province(options);
      }
      this.$parentComponent.add(this.$amapComponent);
    },
    destroyComponent() {
      if(!this.parentInstance.isDestroy){
        this.$parentComponent.remove(this.$amapComponent);
      }
      this.$amapComponent = null;
      this.$parentComponent = null;
    },
    __SOC(value){
      this.$amapComponent.setSOC(value);
    }
  },
  render(){
    return null;
  }
});
</script>
