<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin} from '@vuemap/vue-amap-util';

export default defineComponent({
  name: 'ElAmapElasticMarker',
  mixins: [registerMixin],
  props: {
    position: {
      type: [Array, Object],
      required: true
    }, // 
    title: {
      type: String
    }, // 
    offset: {
      type: [Array, Object]
    }, //  [0,0] Markerpositionmarkeranchoranchorpositionmarkerpositionmarker
    clickable: {
      type: Boolean,
      default: true
    }, // : true
    draggable: {
      type: Boolean,
      default: false
    }, // false
    bubble: {
      type: Boolean,
      default: false
    }, //  false
    zooms: {
      type: Array
    }, // zooms: [2, 20]
    cursor: {
      type: String
    }, // 'pointer'
    topWhenClick: {
      type: Boolean,
      default: false
    }, // markerfalse 
    zoomStyleMapping: {
      type: Object
    }, // styles{14:0,15:0,16:1,17:1,}1415styles016-17
    styles: {
      type: Array
    }, // 
    extData: {
      type: Object,
      default: () => null
    }
  },
  emits: ['update:position'],
  data() {
    return {
      converters: {
      },
    };
  },
  methods: {
    __initComponent(options) {
      return new Promise<void>((resolve) => {
        AMap.plugin(['AMap.ElasticMarker'], () => {
          this.$amapComponent = new AMap.ElasticMarker(options);
          this.$parentComponent.add(this.$amapComponent);
          this.bindModelEvents();
          resolve();
        });
      });
    },
    bindModelEvents(){
      this.$amapComponent.on('dragend',() => {
        this.emitPosition();
      });
      this.$amapComponent.on('touchend',() => {
        this.emitPosition();
      });
    },
    emitPosition(){
      const position = this.$amapComponent.getPosition();
      this.$emit('update:position', position.toArray());
    },
    destroyComponent() {
      // this.$parentComponent.remove(this.$amapComponent);
      if(!this.parentInstance.isDestroy){
        this.$parentComponent.remove(this.$amapComponent);
      }
      this.$amapComponent = null;
      this.$parentComponent = null;
    }
  },
  render() {
    return null;
  }
});
</script>
