<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin} from '@vuemap/vue-amap-util';

export default defineComponent({
  name: 'ElAmapMarkerCluster',
  mixins: [registerMixin],
  props: {
    points: {
      type: Array,
      required: true
    }, // 
    gridSize: {
      type: Number
    }, // 60
    maxZoom: {
      type: Number
    }, //  18 18 18 
    averageCenter: {
      type: Boolean,
      default: true
    }, //  true
    clusterByZoomChange: {
      type: Boolean,
      default: false
    }, //  false
    styles: {
      type: Array
    }, // 
    renderClusterMarker: {
      type: Function
    }, // API  renderClusterMarker  styles 
    renderMarker: {
      type: Function
    } // API 
  },
  data() {
    return {
      converters: {
      },
    };
  },
  methods: {
    __initComponent(options) {
      return new Promise<void>((resolve) => {
        AMap.plugin(['AMap.MarkerCluster'], () => {
          const points = options.points;
          delete options.points;
          this.$amapComponent = new AMap.MarkerCluster(this.$parentComponent, points, options);
          resolve();
        });
      });
    },
    destroyComponent() {
      // this.$amapComponent.setData([])
      this.$amapComponent.setMap(null);
      this.$amapComponent = null;
      this.$parentComponent = null;
    },
    __points(value) {
      if (this.$amapComponent) {
        this.$amapComponent.setData(value);
      }
    }
  },
  render() {
    return null;
  }
});
</script>
