<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin} from '@vuemap/vue-amap-util';

export default defineComponent({
  name: 'ElAmapText',
  mixins: [registerMixin],
  props: {
    position: {
      type: [Array, Object],
      required: true
    }, // 
    text: {
      type: String
    }, // 
    title: {
      type: String
    }, // 
    offset: {
      type: [Array, Object]
    }, //  [0,0] Markerpositionmarkeranchoranchorpositionmarkerpositionmarker
    anchor: {
      type: [String, Array]
    }, // 'top-left','top-center','top-right', 'middle-left', 'center', 'middle-right', 'bottom-left', 'bottom-center', 'bottom-right'
    angle: {
      type: Number
    }, // 0 angleCSS3IE9
    clickable: {
      type: Boolean,
      default: true
    }, // : true
    draggable: {
      type: Boolean,
      default: false
    }, // false
    bubble: {
      type: Boolean,
      default: false
    }, //  false
    zooms: {
      type: Array
    }, // zooms: [2, 20]
    cursor: {
      type: String
    }, // 'pointer'
    topWhenClick: {
      type: Boolean,
      default: false
    }, // markerfalse 
    textStyle: {
      type: Object
    }, // Objectcss:{'background-color':'red'}
    extData: null
  },
  emits: ['update:position'],
  data() {
    return {
      propsRedirect: {
        textStyle: 'style'
      },
      converters: {
      },
    };
  },
  methods: {
    __initComponent(options) {
      this.$amapComponent = new AMap.Text(options);
      this.$parentComponent.add(this.$amapComponent);
      this.bindModelEvents();
    },
    bindModelEvents(){
      this.$amapComponent.on('dragend',() => {
        this.emitPosition();
      });
      this.$amapComponent.on('touchend',() => {
        this.emitPosition();
      });
    },
    emitPosition(){
      const position = this.$amapComponent.getPosition();
      this.$emit('update:position', position.toArray());
    },
    destroyComponent() {
      this.$amapComponent.setMap(null);
      // this.$parentComponent.remove(this.$amapComponent);
      this.$amapComponent = null;
      this.$parentComponent = null;
    }
  },
  render() {
    return null;
  }
});
</script>
