<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin} from '@vuemap/vue-amap-util';

export default defineComponent({
  name: 'ElAmapLabelMarker',
  mixins: [registerMixin],
  props: {
    name: {
      type: String
    }, //  opts.text.content
    position: {
      type: [Array, Object],
      required: true
    }, // 
    zooms: {
      type: Array
    }, // zooms: [2, 20]
    opacity: {
      type: Number
    }, // : 1
    rank: {
      type: Number
    }, //  labelsLayer rank  rank 1
    icon: {
      type: Object
    }, // 
    text: {
      type: Object
    }, // 
    extData: null
  },
  data() {
    return {
      converters: {
      },
    };
  },
  methods: {
    __initComponent(options) {
      this.$amapComponent = new AMap.LabelMarker(options);
      this.$parentComponent.add(this.$amapComponent);
      // console.log('this.$amapComponent: ', this.$amapComponent);
    },
    destroyComponent() {
      // this.$amapComponent.remove();
      // this.$parentComponent.remove(this.$amapComponent);
      if(!this.parentInstance.isDestroy){
        this.$parentComponent.remove(this.$amapComponent);
      }
      this.$amapComponent = null;
      this.$parentComponent = null;
    }
  },
  render() {
    return null
  }
});
</script>
