<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin} from '@vuemap/vue-amap-util';

export default defineComponent({
  name: 'ElAmapLayerFlexible',
  mixins: [registerMixin],
  props: {
    cacheSize: {
      type: Number
    }, // 
    createTile: {
      type: Function
    }, // APIxyz 256A(imgcanvas)success(A)fail()
    zooms: {
      type: Array
    }, //  [2-30]
    opacity: {
      type: Array
    }, //  [0,1] 01 [0,1]
    tileSize: {
      type: Number
    } //  256  128  64256
  },
  data() {
    return {
    };
  },
  methods: {
    __initComponent(options) {
      this.$amapComponent = new AMap.TileLayer.Flexible(options);
      this.$amapComponent.setMap(this.$parentComponent);
    },
    destroyComponent() {
      this.$amapComponent.destroy();
      this.$amapComponent.setMap(null);
      this.$amapComponent = null;
      this.$parentComponent = null;
    }
  },
  render(){
    return null;
  }
});
</script>
