<template>
  <div style="display: none;">
    <div ref="content">
      <slot />
    </div>
  </div>
</template>
<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin,isMapInstance, isOverlayGroupInstance} from '@vuemap/vue-amap-util';


export default defineComponent({
  name: 'ElAmapMarker',
  mixins: [registerMixin],
  props: {
    position: {
      type: [Array, Object],
      required: true
    }, // 
    icon: {
      type: [String, Object]
    }, // Iconcontent
    content: {
      type: [String, HTMLElement]
    }, // HTMLHTML DOMcontenticon
    title: {
      type: String
    }, // 
    offset: {
      type: [Array, Object]
    }, //  [0,0] Markerpositionmarkeranchoranchorpositionmarkerpositionmarker
    anchor: {
      type: [String, Array]
    }, // 'top-left','top-center','top-right', 'middle-left', 'center', 'middle-right', 'bottom-left', 'bottom-center', 'bottom-right'
    angle: {
      type: Number
    }, // 0
    clickable: {
      type: Boolean,
      default: true
    }, // : true
    draggable: {
      type: Boolean,
      default: false
    }, // false
    bubble: {
      type: Boolean,
      default: false
    }, //  false
    zooms: {
      type: Array
    }, // zooms: [2, 20]
    cursor: {
      type: String
    }, // 'pointer'
    topWhenClick: {
      type: Boolean,
      default: false
    }, // markerfalse 
    label: {
      type: Object
    }, // 
    extData: null
  },
  emits: ['update:position'],
  data() {
    return {
      withSlot: false,
      converters: {
      },
    };
  },
  /*updated() {
    if(this.withSlot && this.$amapComponent){
      this.$amapComponent.setContent(this.$refs.content);
    }
  },*/
  methods: {
    __initComponent(options) {
      if (this.$slots.default && this.$slots.default().length > 0) {
        this.withSlot = true;
        options.content = this.getSlotContent();
      }
      this.$amapComponent = new AMap.Marker(options);
      if (isMapInstance(this.$parentComponent)) {
        this.$parentComponent.add(this.$amapComponent);
      } else if (isOverlayGroupInstance(this.$parentComponent)) {
        this.$parentComponent.addOverlay(this.$amapComponent);
      }
      if(this.withSlot){
        // 
        const config = { attributes: true, childList: true, subtree: true };
        // 
        const observer = new MutationObserver(() => {
          this.$amapComponent.setContent(this.getSlotContent());
        });
        observer.observe(this.$refs.content as Node, config);
        this.observer = observer;
      }
      this.bindModelEvents();
    },
    getSlotContent(){
      return (this.$refs.content as HTMLDivElement);
    },
    bindModelEvents(){
      this.$amapComponent.on('dragend',() => {
        this.emitPosition();
      });
      this.$amapComponent.on('touchend',() => {
        this.emitPosition();
      });
    },
    emitPosition(){
      const position = this.$amapComponent.getPosition();
      this.$emit('update:position', position.toArray());
    },
    destroyComponent() {
      if(this.observer){
        this.observer.disconnect();
        this.observer = null;
      }
      this.$amapComponent.setMap(null);
      /*if (isMapInstance(this.$parentComponent)) {
        this.$amapComponent.setMap(null);
        // this.$parentComponent.remove(this.$amapComponent);
      } else if (isOverlayGroupInstance(this.$parentComponent)) {
        this.$parentComponent.removeOverlay(this.$amapComponent);
      }*/
      this.$amapComponent = null;
      this.$parentComponent = null;
    }
  }
});
</script>
