<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin} from '@vuemap/vue-amap-util';

export default defineComponent({
  name: 'ElAmapLayerHeatMap',
  mixins: [registerMixin],
  props: {
    radius: {
      type: Number
    }, // 30pixel
    gradient: {
      type: Object
    }, // {0.4:'rgb(0, 255, 255)',0.85:'rgb(100, 0, 255)',}, key  [0,1] value heatmap.js
    zooms: {
      type: Array
    }, //  [2-30]
    opacity: {
      type: Array
    }, //  [0,1] 01 [0,1]
    config: {
      type: Object
    }, // 3D
    dataSet: {
      type: Object
    }// 
  },
  data() {
    return {
      propsRedirect: {
        config: '3d'
      }
    };
  },
  methods: {
    __initComponent(options) {
      return new Promise<void>((resolve) => {
        AMap.plugin(['AMap.HeatMap'], () => {
          delete options.dataSet;
          this.$amapComponent = new AMap.HeatMap(this.$parentComponent, options);
          if (this.dataSet) {
            this.$amapComponent.setDataSet(this.dataSet);
          }
          resolve();
        });
      });
    },
    destroyComponent() {
      this.$amapComponent.setDataSet({
        data: []
      });
      this.$amapComponent.setMap(null);
      this.$amapComponent = null;
      this.$parentComponent = null;
    }
  },
  render(){
    return null;
  }
});
</script>
