<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin} from '@vuemap/vue-amap-util';
export default defineComponent({
  name: 'ElAmapControlGeolocation',
  mixins: [registerMixin],
  props: {
    position: {
      type: String
    },//"RB".'LT': , 'RT': , 'LB': , 'RB': 
    offset: {
      type: Array
    },// [2,2]
    borderColor: {
      type: String
    },//CSS'silver'
    borderRadius: {
      type: String
    },//CSS'5px'
    buttonSize: {
      type: String
    },//CSS'12px'
    convert: {
      type: Boolean,
      default: true
    },//
    enableHighAccuracy: {
      type: Boolean,
      default: false
    },//false
    timeout: {
      type: Number
    },//
    maximumAge: {
      type: Number
    },//
    showButton: {
      type: Boolean,
      default: true
    },//true
    showCircle: {
      type: Boolean,
      default: true
    },//true
    showMarker: {
      type: Boolean,
      default: true
    },//true
    markerOptions: {
      type: Object
    },//
    circleOptions: {
      type: Object
    },//CircleOptions
    panToLocation: {
      type: Boolean,
      default: true
    },//
    zoomToAccuracy: {
      type: Boolean,
      default: true
    },//
    GeoLocationFirst: {
      type: Boolean,
      default: true
    },//H5false
    noIpLocate: {
      type: Number
    },//IP00: 1: 2:PC 3:
    noGeoLocation: {
      type: Number
    },//00: 1: 2:PC 3:
    useNative: {
      type: Boolean,
      default: false
    },//SDKsdk
    getCityWhenFail: {
      type: Boolean,
      default: false
    },//
    needAddress: {
      type: Boolean,
      default: false
    },//
    extensions: {
      type: String,
      validator(value : string) {
        // 
        return ['base', 'all'].indexOf(value) !== -1;
      }
    },//'base''all'
  },
  emits: ['complete'],
  data() {
    return {
    };
  },
  methods: {
    __initComponent(options) {
      return new Promise<void>((resolve) => {
        this.$parentComponent.plugin(['AMap.Geolocation'], () => {
          this.$amapComponent = new AMap.Geolocation(options);
          this.$parentComponent.addControl(this.$amapComponent);
          this.$amapComponent.on('complete', (e) => {
            this.$emit('complete', e);
          })
          resolve();
        });
      });
    },
    destroyComponent() {
      if (this.$amapComponent && this.$parentComponent) {
        if(!this.parentInstance.isDestroy){
          this.$parentComponent.removeControl(this.$amapComponent);
        }
        this.$amapComponent = null;
        this.$parentComponent = null;
      }
    },
  },
  render(){
    return null;
  }
});
</script>
