<template>
  <div class="el-vue-amap-container">
    <div class="el-vue-amap" />
    <slot />
  </div>
</template>
<script lang="ts">
import {defineComponent} from "vue";
import {guid,registerMixin} from '@vuemap/vue-amap-util';
import {lazyAMapApiLoaderInstance} from '../../services';

export default defineComponent ({
  name: 'ElAmap',
  mixins: [registerMixin],
  provide() {
    return {
      parentInstance: this
    };
  },
  props: {
    vid: {
      type: String
    }, // ID
    center: {
      type: Array
    }, // 
    zoom: {
      type: Number
    }, // centerlevel
    rotation: {
      type: Number
    }, //  [0-360] 0
    pitch: {
      type: Number
    }, //  0 zoom 2D 
    viewMode: {
      type: String
    }, // , 2D3D3D 3D 
    features: {
      type: Array
    }, // , 'bg''point'POI'road''building'['bg','point','road','building']
    layers: {
      type: Array
    }, //     TileLayer  AMap.createDefaultLayer()
    zooms: {
      type: Array
    }, // ,  [2, 20]  [2 ~ 30]
    resizeEnable: {
      type: Boolean,
      default: true
    }, // false setStatus/getStatus 
    dragEnable: {
      type: Boolean,
      default: true
    }, // ,  true setStatus/getStatus 
    zoomEnable: {
      type: Boolean,
      default: true
    }, //  true setStatus/getStatus 
    jogEnable: {
      type: Boolean,
      default: true
    }, // truesetStatus/getStatus 
    pitchEnable: {
      type: Boolean,
      default: true
    }, // , 3D  true, 2D setStatus/getStatus 
    rotateEnable: {
      type: Boolean,
      default: true
    }, // , truesetStatus/getStatus 
    animateEnable: {
      type: Boolean,
      default: true
    }, // panBypanTosetCentersetZoomAndCenter, , , true, 
    keyboardEnable: {
      type: Boolean,
      default: true
    }, // , true, "+""-", Ctrl+Ctrl+setStatus/getStatus 
    doubleClickZoom: {
      type: Boolean,
      default: true
    }, // , truesetStatus/getStatus 
    scrollWheel: {
      type: Boolean,
      default: true
    }, // truesetStatus/getStatus 
    touchZoom: {
      type: Boolean,
      default: true
    }, // truefalse
    touchZoomCenter: {
      type: Number
    }, // touchZoomCenter=11
    showLabel: {
      type: Boolean,
      default: true
    }, //  POI  true
    defaultCursor: {
      type: String
    }, //  defaultCursor  CSS  cursor 
    isHotspot: {
      type: Boolean
    }, //  hover PCtrue,  false
    mapStyle: {
      type: String
    }, //   "amap://styles/d6bf8c1d69cea9f5c696185ad4ac4c86"  ,"amap://styles/grey" 
    wallColor: {
      type: [String, Array]
    }, // 
    roofColor: {
      type: [String, Array]
    }, // 
    showBuildingBlock: {
      type: Boolean,
      default: true
    }, //  3D  true
    showIndoorMap: {
      type: Boolean,
      default: false
    }, //  false
    skyColor: {
      type: [String, Array]
    }, // 3D 
    labelRejectMask: {
      type: Boolean,
      default: false
    }, // 
    mask: {
      type: Array
    }, //  Map 3D 
    WebGLParams: {
      type: Object
    },// WebGL eg: preserveDrawingBuffer
    terrain: {
      type: Boolean,
      default: false
    }, //
  },
  emits: ['update:zoom', 'update:center', 'update:rotation', 'update:pitch'],

  data() {
    return {
    };
  },

  computed: {
  },

  mounted() {
    this.createMap();
  },
  beforeUnmount() {
    if (this.$amapComponent) {
      this.$amapComponent.destroy();
      this.$amapComponent = null;
      this.$parentComponent = null;
    }
  },
  methods: {
    createMap() {
      lazyAMapApiLoaderInstance.then(() => {
        const mapElement = this.$el.querySelector('.el-vue-amap');
        const elementID = this.vid || guid();
        mapElement.id = elementID;
        this.$nextTick(() => {
          this.$amapComponent = new AMap.Map(elementID, this.convertProps());
          this.register();
          this.bindModelEvents();
        })
      }).catch(e => {
        console.warn('init map error: ', e);
      });
    },
    bindModelEvents(){
      this.$amapComponent.on('zoomchange',() => {
        this.$emit('update:zoom', this.$amapComponent.getZoom());
      })
      this.$amapComponent.on('rotatechange',() => {
        this.$emit('update:rotation', this.$amapComponent.getRotation());
        this.$emit('update:pitch', this.$amapComponent.getPitch());
      })
      this.$amapComponent.on('dragging',() => {
        this.$emit('update:center', this.getCenter());
        this.$emit('update:pitch', this.$amapComponent.getPitch());
      })
      this.$amapComponent.on('touchmove',() => {
        this.$emit('update:center', this.getCenter());
      })
    },
    getCenter(){
      const center = this.$amapComponent.getCenter();
      return [center.lng, center.lat];
    },
    __dragEnable(flag){
      if(this.$amapComponent){
        this.$amapComponent.setStatus({dragEnable: flag});
      }
    },
    __zoomEnable(flag){
      if(this.$amapComponent){
        this.$amapComponent.setStatus({zoomEnable: flag});
      }
    },
    __jogEnable(flag){
      if(this.$amapComponent){
        this.$amapComponent.setStatus({jogEnable: flag});
      }
    },
    __keyboardEnable(flag){
      if(this.$amapComponent){
        this.$amapComponent.setStatus({keyboardEnable: flag});
      }
    },
    __doubleClickZoom(flag){
      if(this.$amapComponent){
        this.$amapComponent.setStatus({doubleClickZoom: flag});
      }
    },
    __scrollWheel(flag){
      if(this.$amapComponent){
        this.$amapComponent.setStatus({scrollWheel: flag});
      }
    },
    __rotateEnable(flag){
      if(this.$amapComponent){
        this.$amapComponent.setStatus({rotateEnable: flag});
      }
    },
    __resizeEnable(flag){
      if(this.$amapComponent){
        this.$amapComponent.setStatus({resizeEnable: flag});
      }
    },
    __showIndoorMap(flag){
      if(this.$amapComponent){
        this.$amapComponent.setStatus({showIndoorMap: flag});
      }
    }
  }
});
</script>

<style lang="scss">
.el-vue-amap-container {
  height: 100%;
  position: relative;

  .el-vue-amap {
    height: 100%;
  }
}
</style>
