<template>
  <div
    v-show="visible && !inputCustom"
    class="el-vue-search-box-container"
  >
    <input
      v-if="!inputCustom"
      :id="saveInputId"
      type="text"
      :placeholder="placeholder"
    >
  </div>
</template>
<script lang="ts">
import {defineComponent} from "vue";
import {registerMixin, guid} from '@vuemap/vue-amap-util';
export default defineComponent({
  name: 'ElAmapSearchBox',
  mixins: [registerMixin],
  props: {
    type: {
      type: String
    },//POI|Poi050000 
    city: {
      type: String
    },//citycodeadcode
    datatype: {
      type: String
    },//all-poi-POIbus-busline-
    citylimit: {
      type: Boolean,
      default: false
    },//all-poi-POIbus-busline-
    inputId: {
      type: String
    },//ID
    inputCustom: {
      type: Boolean,
      default: false
    }, //inputinputId
    outputId: {
      type: String
    },//dividinput
    outPutDirAuto: {
      type: Boolean,
      default: true
    },//trueinputinput
    closeResultOnScroll: {
      type: Boolean,
      default: true
    },// true
    lang: {
      type: String
    },// 'zh_cn'
    placeholder: {
      type: String
    }
  },
  data() {
    return {
      saveInputId: '',
      saveVisible: true,
    };
  },
  created() {
    this.saveInputId = guid();
  },
  methods: {
    __initComponent(options) {
      if (options.inputId) {
        this.saveInputId = options.inputId;
        delete options.inputId;
      }
      if(options.visible){
        this.saveVisible = options.visible;
      }
      options.input = this.saveInputId;
      if (options.outputId) {
        options.output = options.outputId;
        delete options.outputId;
      }
      return new Promise<void>((resolve) => {
        this.$parentComponent.plugin(['AMap.AutoComplete'], () => {
          this.$amapComponent = new AMap.AutoComplete(options);
          resolve();
        });
      });
    },
    __visible(flag) {
      this.saveVisible = flag;
    },
    __citylimit(flag) {
      this.$amapComponent.setCityLimit(flag);
    }
  }
});
</script>
<style lang="scss">
.el-vue-search-box-container {
  position: absolute;
  left: 16px;
  top: 16px;
  z-index: 10;
  width: 360px;
  height: 45px;
  background: #fff;
  box-shadow: 0 2px 2px rgba(0,0,0,.15);
  border-radius: 2px 3px 3px 2px;

  input {
    height: 100%;
    width: 100%;
    line-height: 45px;
    letter-spacing: .5px;
    font-size: 14px;
    text-indent: 10px;
    box-sizing: border-box;
    border: none;
    outline: none;
  }

}
</style>
